
























































































export default {
	data() {
		return {
			inputValue: '',
			changeValue: ''
		};
	},
	methods: {
		onKeyInput(e) {
			this.inputValue = e.target.value;
		},
		replaceInput(e) {
			let value = e.target.value;
			if (value == '11') {
				this.changeValue = '2';
			}
		},
		//输入123隐藏键盘
		hideKeyboard(e) {
			let value = e.target.value;
			debugger;
			if (value == '123') {
				uni.hideKeyboard();
			}
		}
	}
};
